.cards-container {
    padding: 10px;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
}

@media screen and (min-width: 820px) {
    .cards-container {
        position: absolute;
        width: 75%;
        right: 0;
    }
}